@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .section-card {
    @apply bg-white p-4 md:p-6 rounded-lg shadow-md;
  }
  
  .section-title {
    @apply text-lg md:text-xl font-semibold text-accent mb-3 md:mb-4;
  }
  
  .section-content {
    @apply text-gray-800 text-base md:text-lg space-y-2;
  }
}

/* 스크롤바 스타일링 */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #666;
}

/* 터치 디바이스 최적화 */
@media (hover: none) {
  .hover\:scale-102 {
    transform: none !important;
  }
}

/* 모바일에서 탭 하이라이트 제거 */
* {
  -webkit-tap-highlight-color: transparent;
}

/* 기본 폰트 사이즈 반응형 조정 */
html {
  font-size: 16px;
}

@media (max-width: 640px) {
  html {
    font-size: 14px;
  }
}

/* 더 나은 텍스트 가독성 */
body {
  @apply text-gray-800;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}